import * as React from "react";
import { useState, useEffect } from "react";

import SinglePanelComponent from "./Components/SinglePanelComponent";

import "./styles/App.css";

import ForeignPressClass from "./Components/defs/ForeignPressClass";

import { io } from "socket.io-client";

export let socket;

let eachUpdate = 0;


export const socketSend = (press, cmd) => {
  socket.emit("msg_gui", {
    receiverId: socket.id,
    protocol: "gui",
    engine: press === "REUTERS" ? "reuter" : press.toLowerCase(),
    data: { cmd: cmd },
  });
};

const dateTimeFormatter = (dateTime) => {
  return (
    dateTime.split(" ")[0].slice(2, 4) +
    "-" +
    dateTime.split(" ")[0].slice(4, 6) +
    "-" +
    dateTime.split(" ")[0].slice(6, 8) +
    " " +
    dateTime.split(" ")[1].slice(0, 2) +
    ":" +
    dateTime.split(" ")[1].slice(2, 4) +
    ":" +
    dateTime.split(" ")[1].slice(4, 6)
  );
};

export default function MyApp(props) {
  let sentfiles = new Set();
  let successFileList = [];
  let failFileList = [];

  const [mp4OpenButtonStatus, setMp4OpenButtonStatus] = useState("true");
  const [scriptOpenButtonStatus, setScriptOpenButtonStatus] = useState("true");
  const [scriptsListState, setScriptsListState] = useState([]);
  const [scriptContentState, setScriptContentState] = useState("");

  const REUTERS = new ForeignPressClass("reuter");
  const APTN = new ForeignPressClass("aptn");
  const CNN = new ForeignPressClass("cnn");

  const [isLoading, setLoadingState] = useState(true);

  const [sttStatus, setSttStatus] = useState(null);
  const [translateStatus, setTranslateStatus] = useState(null);
  const [onoffButtonStatus, setOnOffButtonStatus] = useState("disable");

  const [reutersFilesList, setReutersFilesList] = useState([]);
  const [aptnFilesList, setAPTNFilesList] = useState([]);
  const [cnnFilesList, setCNNFilesList] = useState([]);

  const incomelistUpdater = () => {
    setLoadingState(true);
    console.log("Updating mp4 files list");
    socketSend("reuter", "<get_incomelist>");
  };

  useEffect(() => {
    socket = io("http://202.31.153.221:50080", {
      cors: { origin: "*" },
    });
    socket.connect();
    socket.on("connect", () => {
      console.log("socketio connected");
      incomelistUpdater();
    });
    socket.on("msg_engine", (recv) => {
      let data = recv.data.split("<br>");
      data.forEach((item) => {
        if (item.includes("stt **")) {
          setSttStatus(item.replace("stt ** ", "") === "False" ? false : true);
          setOnOffButtonStatus("enable");
        }
        if (item.includes("translation **")) {
          setTranslateStatus(
            item.replace("translation ** ", "") === "False" ? false : true
          );
        }
      });
    });
    return () => {
      socket.off("connect");
      socket.off("msg_engine");
      socket.disconnect();
    };
  }, []);

  const data_processor = (recv) => {
    let data = recv.replace("<start_transfer>", "");
    if (data.includes("<ftpincomelist>")) {
      sentfiles = new Set();
      data
        .split("\n")
        .filter((file) => file.split(".").length > 1)
        .filter((file) => file.split(".")[1].toLowerCase() === "xml")
        .forEach((file) => {
          if (file?.split("/")[2]?.split(".")[0]) {
            sentfiles.add(file?.split("/")[2]?.split(".")[0]);
          }
        });

      socketSend("reuter", "<get_successlist>");
    }

    if (data.includes("<ftpsuccesslist>")) {
      data = data.split("\n");
      data = data.splice(0, data.length - 1);
      data.forEach((item, index) => {
        try {
          successFileList.push(item.split("/")[2].split(".")[0]);
        } catch (e) {
          console.log("Error on info of" + item + ", index is" + index);
        }
      });
      socketSend("reuter", "<get_errorlist>");
    }

    if (data.includes("<ftperrorlist")) {
      data = data.split("\n");
      data = data.splice(0, data.length - 1);
      data.forEach((item, index) => {
        try {
          failFileList.push(item.split("/")[2].split(".")[0]);
        } catch (e) {
          console.log("Error on info of " + item + ", index is" + index);
        }
      });
      socketSend("reuter", "<get_income>");
      socketSend("aptn", "<get_income>");
      socketSend("cnn", "<get_income>");
    }
    if (data.includes("<incomelist>")) {
      let press
      if (data.includes("agent_APTN")) {
        APTN.fileList = APTN.fileList.slice(0, 0);
        press = "APTN"
      }
      else if (data.includes("agent_CNN")) {
        CNN.fileList = CNN.fileList.slice(0, 0);
        press = "CNN"
      }
      else if (data.includes("agent_REUTERS")) {
        REUTERS.fileList = REUTERS.fileList.slice(0, 0);
        press = "REUTERS"
      }
      data = data.split("\n");
      data = data.slice(0, data.length - 1);

      data.forEach((file) => {

        let filename = file.split("\\")[2].split("|")[0];
        let hash
        let title = filename.slice(0, filename.length - 4);
        if (file.includes("agent_APTN")) {
          hash = title.split("_")[0];
          title = title.split("_")[1]
        }
        else if (file.includes("agent_CNN")) {
          hash = title.split("_")[1];
          title = filename.slice(0, filename.length - 4);
          title = title.split("CNNA")[0]
          title = title.replace(title.split("_")[0], "").replace("_", "")
          title = title.replace(title.split("_")[0], "").replace("_", "")
          title = title.slice(0, title.length - 1)
        }
        else if (file.includes("agent_REUTERS")) {
          hash = title.split("_")[2];
          title = title.split("_")[title.split("_").length - 1]
        }


        let dateTime = dateTimeFormatter(file.split("|")[1]);
        let isSent = false;
        for (let i = 0; i < sentfiles.length; i++) {
          if (sentfiles[i].includes(filename.split(".")[0])) {
            isSent = true;
            break;
          }
        }

        let ingestStatus = null;
        for (let i = 0; i < successFileList.length; i++) {
          if (successFileList[i].includes(filename.split(".")[0])) {
            ingestStatus = "succeeded";
            isSent = true;
            break;
          }
        }
        for (let i = 0; i < failFileList.length; i++) {
          if (failFileList[i].includes(filename.split(".")[0])) {
            ingestStatus = "fail";
            isSent = true;
          }
        }

        let fileListObject = {
          hash: hash,
          filename: filename,
          dateTime: dateTime,
          title: title,
          isSent: isSent,
          ingestStatus: ingestStatus,
        }
        switch (press) {
          case "APTN":
            fileListObject.press = "APTN"
            fileListObject.id = "APTN" + APTN.fileList.length
            APTN.fileList.push(fileListObject);
            break;
          case "CNN":
            fileListObject.press = "CNN"
            fileListObject.id = "CNN" + CNN.fileList.length
            CNN.fileList.push(fileListObject);
            break;
          case "REUTERS":
            fileListObject.press = "REUTERS"
            fileListObject.id = "REUTERS" + REUTERS.fileList.length
            REUTERS.fileList.push(fileListObject);
            break;
          default:
            break
        }

      })
      switch (press) {
        case "APTN":
          setAPTNFilesList(APTN.fileList);
          break;
        case "CNN":
          setCNNFilesList(CNN.fileList);
          break;
        case "REUTERS":
          setReutersFilesList(REUTERS.fileList);
          break;
        default:
          break
      }

      eachUpdate++;
      console.log(eachUpdate);
      if (eachUpdate % 3 === 0 && eachUpdate !== 0) {
        setLoadingState(false);
        eachUpdate = 0;
      }
      setLoadingState(false);
    }

    if (data.includes("<scriptlist>")) {
      let scriptList = [];
      let tempMsgList = data.split("\n");
      tempMsgList = tempMsgList.slice(0, tempMsgList.length - 1);
      tempMsgList.forEach((item, index) => {
        let fullPath = tempMsgList[index].split("|")[0];
        let filename = fullPath
          .split("\\")
        [fullPath.split("\\").length - 1].trim();
        let title = filename.slice(0, filename.length - 4);
        let hash = data.includes("agent_REUTERS")
          ? title.split("_")[2].replace("RW", "LWD")
          : data.includes("agent_APTN")
            ? title.split("_")[0]
            : title.split("_")[0];
        scriptList.push({
          press: data.includes("agent_REUTERS")
            ? "REUTERS"
            : data.includes("agent_APTN")
              ? "APTN"
              : "CNN",
          hash: hash,
          id: scriptList.length + 1,
          scriptFilename: filename,
          dateTime: dateTimeFormatter(item.split("|")[1]),
          index: filename,
        });
      });
      scriptList.reverse();
      setScriptsListState(scriptList);
      let fullDir = `d:\\agent_${scriptList[0].press}\\`;
      socketSend(
        scriptList[0].press,
        `<get_scdata><${fullDir + scriptList[0].scriptFilename}>`
      );
    }

    if (data.includes("<scriptdata>")) {
      setScriptContentState(data.replace("<finish_transfer><scriptdata>", ""));
    }
  };

  useEffect(() => {
    socket.on("reply_engine", function (data) {
      data_processor(data);
    });

    function periodicMp4FileListUpdate() {
      return setInterval(() => {
        incomelistUpdater();
      }, 5 * 60 * 1000);
    }
    periodicMp4FileListUpdate();
    return () => {
      clearInterval(periodicMp4FileListUpdate);
      socket.off("reply_engine");
    };
  }, []);

  return (
    <SinglePanelComponent
      reutersFilesList={reutersFilesList}
      aptnFilesList={aptnFilesList}
      cnnFilesList={cnnFilesList}
      isLoading={isLoading}
      setLoadingState={setLoadingState}
      sttStatus={sttStatus}
      setSttStatus={setSttStatus}
      translateStatus={translateStatus}
      setTranslateStatus={setTranslateStatus}
      mp4FileListUpdater={incomelistUpdater}
      setOnOffButtonStatus={setOnOffButtonStatus}
      onoffButtonStatus={onoffButtonStatus}
      mp4OpenButtonStatus={mp4OpenButtonStatus}
      setMp4OpenButtonStatus={setMp4OpenButtonStatus}
      scriptOpenButtonStatus={scriptOpenButtonStatus}
      setScriptOpenButtonStatus={setScriptOpenButtonStatus}
      scriptsListState={scriptsListState}
      setScriptsListState={setScriptsListState}
      scriptContentState={scriptContentState}
      setScriptContentState={setScriptContentState}
    />
  );
}

export const nowLog = (msg) => {
  let now = new Date(Date.now());
  now =
    now.getHours().toString() +
    "-" +
    now.getMinutes().toString() +
    "-" +
    now.getSeconds().toString();
  console.log(`${now} ${msg}`);
};
